.temp {
	width:130px;
	height:130px;
	border-radius: 40%;
	margin: 1vw;
}

/* Sunny */

.night { 
	position: relative;
	background:linear-gradient( to top right,rgb(32, 35, 41) 0%,
	rgb(40, 73, 100) 50%);
	box-shadow: 1px 1px 30px rgba(161, 173, 173, 0.644);
}

.moon {
	position: absolute;
	top:-5%;
	left:55%;
	width:50px;
	height: 50px;
	border-radius: 100%;
	background:linear-gradient( to bottom left, rgb(245, 241, 208) 0%,
	rgb(236, 246, 255) 90%);
	box-shadow: 1px 1px 30px rgba(224, 222, 196, 0.829);
	animation: sunshine 3s infinite linear ;
}

/* Hazey */

.hazey-moon {
    position: absolute;
	top:10%;
	left:50%;
	width:50px;
	height: 50px;
	border-radius: 100%;
	background:linear-gradient( to bottom left, rgb(221, 221, 211) 0%,
	rgb(235, 231, 213) 90%);
	box-shadow: 1px 1px 30px rgba(37, 49, 73, 0.4);
}

.hazey-night { 
	position: relative;
	background:linear-gradient( to top right, rgb(40, 59, 70) 0%,
	rgb(78, 106, 121) 60%);
	box-shadow: 1px 1px 30px rgb(99, 119, 131, .8);
}

.haze-night {
    background-color: #dddfe2;
    background-image: -webkit-linear-gradient(hsla(0,0%,0%,0), hsla(0,0%,0%,.1));
    border-radius: 1em;
    display: inline-block;
    height: 1em;
    left: 50%;
    margin-left: -1.5em;
    position: absolute;
    top: 30%;
    width: 3em;
    -webkit-filter: drop-shadow(0 2px 3px hsla(0,0%,0%,.25));
    animation: move 5s infinite linear ;
}

.haze2-night  {
    background-color: #dddfe2;
    background-image: -webkit-linear-gradient(hsla(0,0%,0%,0), hsla(0,0%,0%,.1));
    border-radius: 1em;
    display: inline-block;
    height: 1em;
    left: 63%;
    margin-left: -1.5em;
    position: absolute;
    top: 50%;
    width: 3em;
    -webkit-filter: drop-shadow(0 2px 3px hsla(0,0%,0%,.25));
    animation: move2 3s infinite linear ;
 }

.haze-night:after, .haze2-night:after,
.haze-night:before, .haze2-night:before {
    background-color: #dddfe2;
    content: '';
    border-radius: 100%;
    position: absolute;
}
.haze-night:after, .haze2-night:after {
    background-image: -webkit-linear-gradient(hsla(0,0%,0%,0) 50%, hsla(0,0%,0%,.025));
    height: 1em;
    right: .4em;
    top: -.5em;
    width: 1em;
}
.haze-night:before, .haze2-night:before {
    background-image: -webkit-linear-gradient(hsla(0,0%,0%,0) 50%, hsla(0,0%,0%,.075));
    height: 1.6em;
    left: .4em;
    top: -.75em;
    width: 1.6em;
}

