/* Snowy */
.snowy {
	position: relative;
	background:linear-gradient( to top right,rgb(68, 108, 153) 0%,
	rgb(163, 210, 255) 90%);
	box-shadow: 1px 1px 30px rgba(66, 65, 65, 0.521);
}

.snowy li {
	position: absolute;
	list-style: none;
	width: 5px;
	height: 5px;
	border-radius: 100px;
	background-color:rgb(238, 238, 238);
	box-shadow: 1px 1px 30px rgb(236, 230, 230);
	animation: fall 3s infinite linear ;
    opacity:0;
}
.snowy li:nth-child(1) {
top:2%;
left:80%;
}

.snowy li:nth-child(2) {
top:5%;
left:70%;
}

.snowy li:nth-child(3) {
top:10%;
left:60%;
animation-delay: 2s;
}

.snowy li:nth-child(4) {
top:10%;
left:80%;
animation-delay: 2s;
}

.snowy li:nth-child(5) {
top:20%;
left:60%;
animation-delay: 0.5s;
}

.snowy li:nth-child(6) {
top:35%;
left:50%;
background-color: #bbb;
animation-delay: 0.5s;
}

.snowy li:nth-child(7) {
top:23%;
left:40%;
background-color: #bbb;
animation-delay: 0.8s;
}

.snowy li:nth-child(8) {
top:5%;
left:50%;
background-color: #bbb;
animation-delay: 0.8s;
}

.snow-head {
position: absolute;
	top:60%;
	left:40%;
	width:40px;
	height: 40px;
	border-radius:60px;
	background-color: rgb(252, 249, 249);
}

.snow-body {
	position: absolute;
	top:50%;
	left:50%;
	width:15px;
	height: 15px;	
	border-radius:65px;
	background-color: white;
}

/* Rain */
.rainy-night
{
	position: relative;
	background:linear-gradient( to top right,rgb(32, 35, 41) 0%,
	rgb(40, 73, 100) 50%);
	box-shadow: 1px 1px 30px rgb(131, 135, 150);

}

.rainy-night li
{
	position: absolute;
	list-style: none;
	width: 2px;
	height: 5px;
	border-radius: 10%;
	background-color:rgba(155, 188, 201, 0.699);
	opacity:0;
	animation: fall 3s infinite linear ;
	transform: rotate(25deg);
}
.rainy-night li:nth-child(1)
{
top:55%;
left:30%;
}

.rainy-night li:nth-child(2)
{
top:50%;
left: 43%;
animation-delay:0.3s;
}

.rainy-night li:nth-child(3)
{
top:40%;
left:37%;
animation-delay:0.6s;
}



@keyframes sunshine {
	50% {
	opacity: 0.6;

	}
	60%
	{
		opacity: 1;
	}
}

@keyframes move {

	50% {
	transform: translateX(-10px);

	}
}

@keyframes move2 {

	50% {
	transform: translateX(-8px);

	}
}

@keyframes fall {
10%
{
	opacity:0.8;
}

	50% {
	opacity:1;
	transform: translate(-10px,30px);

	}
	100%
	{
		transform: translate(-25px,70px);
	}
}
